.App {
   text-align: center;
 }
 
 .App-logo {
   animation: App-logo-spin infinite 20s linear;
   height: 80px;
 }
 
 .App-header {
   background-color: #222;
   height: 150px;
   padding: 20px;
   color: white;
 }
 
 .App-intro {
   font-size: large;
 }
 .hometxt {
  text-align: center;
  margin-left: 15%;
  margin-right: 15%;
 }
 
 @keyframes App-logo-spin {
   from { transform: rotate(0deg); }
   to { transform: rotate(360deg); }
 }
 
 .nav-padding {
   padding-right: 1rem;
 }
 
 .signout {
   text-align: right;
 }
 
 .logged-in {
   padding-top: 20px
 }